:root {
    --primary: #A84FFF;
    --secondary: #D81E5B;
    --dark: #131A26;
    --light: #EEEE;
    --grey: #888888;

    --font: 'Fira Sans', sans-serif;
    --font-quote: 'Fira Code', monospace;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--font);
}

body {
    background-color: var(--light);
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

.quote {
    position: relative;
}

.anime, .character {
    display: inline-block;
    padding: 0.75rem 1.5rem;
    background-color: var(--secondary);
    color: var(--light);
    border-radius: 1.5rem;
    font-weight: 700;
    font-size: 1.4rem;
    letter-spacing: 1px;
    max-width: 360px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.anime {
    background-color: var(--secondary);
    font-weight: 400;
    transform: translate(-25%, 50%);
}

.character {
    background-color: var(--primary);
    font-weight: 700;
    transform: translate(-25%, 50%);
    position: absolute;
    right: 0;
    bottom: 0;
}

blockquote {
    font-family: var(--font-quote);
    max-width: 720px;
    font-size: 2rem;
    background-color: var(--dark);
    padding: 3rem;
    border-radius: 1.5rem;
    color: #FFF;
}


button {
    appearance: none;
    border: none;
    outline: none;

    background-color: var(--primary);
    color: #FFFFFF;
    padding: 0.75rem 1.5rem;
    border-radius: 3rem;
    margin-top: 64px;
    margin-bottom: 64px;
    transition: 0.4s;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 700;
}

button:hover {
    background-color: var(--secondary);
}
